import React from 'react';
import { View, Text, FlatList, StyleSheet } from 'react-native';

const ResultScreen = ({ questions, score, selectedOptions }) => {
  return (
    <View style={styles.container}>
      <Text style={styles.scoreText}>Your Score: {score} / {questions.length}</Text>
      <FlatList
        data={questions}
        keyExtractor={(item, index) => index.toString()}
        renderItem={({ item, index }) => {
          const isCorrect = selectedOptions[index] === item.correctAnswer;
          return (
            <View style={styles.resultContainer}>
              <Text style={styles.questionText}>{item.question}</Text>
              {item.options.map((option, optionIndex) => (
                <View key={optionIndex}>
                  <Text style={[styles.optionText, 
                                selectedOptions[index] === option ? 
                                  (isCorrect ? styles.correct : styles.incorrect) : null]}>
                    {option}
                  </Text>
                </View>
              ))}
              <Text style={styles.correctAnswerText}>Correct Answer: {item.correctAnswer}</Text>
            </View>
          );
        }}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    padding: 16,
  },
  scoreText: {
    fontSize: 24,
    textAlign: 'center',
    marginVertical: 16,
  },
  resultContainer: {
    backgroundColor: '#ecf0f1',
    padding: 16,
    borderRadius: 8,
    marginVertical: 8,
  },
  questionText: {
    fontSize: 18,
    marginBottom: 8,
  },
  optionText: {
    fontSize: 18,
    marginBottom: 4,
  },
  correctAnswerText: {
    color: 'green',
    fontSize: 18,
    marginTop: 8,
  },
  correct: {
    color: 'green',
    fontSize: 18,
  },
  incorrect: {
    color: 'red',
    fontSize: 18,
  },
});

export default ResultScreen;
