import React, { useState } from 'react';
import { View, Text, TouchableOpacity, StyleSheet } from 'react-native';

const QuizScreen = ({ questions, endQuiz }) => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [selectedOptions, setSelectedOptions] = useState({});

  const handleOptionPress = (option) => {
    const updatedSelectedOptions = { ...selectedOptions, [currentQuestionIndex]: option };
    setSelectedOptions(updatedSelectedOptions);

    // Calculate score based on correct answers
    let currentScore = 0;
    Object.keys(updatedSelectedOptions).forEach((key) => {
      if (questions[key].correctAnswer === updatedSelectedOptions[key]) {
        currentScore++;
      }
    });

    setScore(currentScore);

    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      endQuiz(currentScore, updatedSelectedOptions);
    }
  };

  const question = questions[currentQuestionIndex];

  return (
    <View style={styles.container}>
      <Text style={styles.questionText}>{question.question}</Text>
      {question.options.map((option, index) => (
        <TouchableOpacity
          key={index}
          style={styles.optionButton}
          onPress={() => handleOptionPress(option)}
        >
          <Text style={styles.optionText}>{option}</Text>
        </TouchableOpacity>
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    padding: 16,
  },
  questionText: {
    fontSize: 24,
    marginBottom: 16,
  },
  optionButton: {
    backgroundColor: '#3498db',
    padding: 16,
    borderRadius: 8,
    marginVertical: 8,
  },
  optionText: {
    color: 'white',
    fontSize: 18,
  },
});

export default QuizScreen;
