import React, { useState } from 'react';
import { SafeAreaView, StyleSheet } from 'react-native';
import HomeScreen from './screens/HomeScreen';
import QuizScreen from './screens/QuizScreen';
import ResultScreen from './screens/ResultScreen';
import questions from './questions';

const App = () => {
  const [screen, setScreen] = useState('home');
  const [score, setScore] = useState(0);
  const [selectedOptions, setSelectedOptions] = useState({});

  const startQuiz = () => {
    setScreen('quiz');
    setScore(0);
    setSelectedOptions({});
  };

  const endQuiz = (finalScore, selectedOptions) => {
    setScore(finalScore);
    setSelectedOptions(selectedOptions);
    setScreen('result');
  };

  return (
    <SafeAreaView style={styles.container}>
      {screen === 'home' && <HomeScreen startQuiz={startQuiz} />}
      {screen === 'quiz' && <QuizScreen questions={questions} endQuiz={endQuiz} />}
      {screen === 'result' && <ResultScreen questions={questions} score={score} selectedOptions={selectedOptions} />}
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

export default App;
